@mixin desktop(){
    @media only screen and (min-width: $display-xxl){
        @content;
    }
}

@mixin laptop(){
    @media only screen and (max-width:$display-xl){
        @content;
    }
}

@mixin laptop-md(){
    @media only screen and(max-width: $display-lg){
        @content;
    }
}

@mixin tablet(){
    @media only screen and(max-width: $display-md){
        @content;
    }
}

@mixin tablet-md(){
    @media only screen and (max-width: $display-sm){
        @content;
    }
}

@mixin tablet-sm(){
    @media only screen and (max-width: $display-xs){
        @content;
    }
}